.formContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: flex-start;
  outline: 2px solid #D0D0D0;
}

.bodyRow{
  display: flex;
  flex-direction: row;
  width: 100%;
}

.headerRow {
  display: flex;
  width: 100%;
  outline: 1px solid #D0D0D0; 
}

.headerItem, .headerItem2 {
  outline: 1px solid #D0D0D0; 
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
} 

.headerNew {
  display: flex;
  flex-direction: row;
}

.headerItem2 {
  display: flex;
  flex-direction: column;
}

.headerItemHalf {
  display: flex;
  flex-direction: row;
  height: 50%;
  width: 100%;
}

.semName {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.yearColumn {
  display: flex;
  flex-direction: column;
  width: 8%;
}

.yearBody {
  display: flex;
  flex-direction: column;
  width: 92%;
}

.inputRow {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: flex-start;
}

.yearItem {
  outline: 1px solid #D0D0D0; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.percentBox {
  width: 40px;
  border: none;
  border: 1px solid #888888;
  border-radius: 5px;
  font-size: 13px;
  text-align: center;
  margin-top: 3px;
}

.detailsContainer {
  display: flex;
  width: 100%;
}

.detailsItem {
  outline: 1px solid #d0d0d0;  
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gradeCalculator {
  display: flex;
  flex-direction: column;
  padding: 10px;
  box-sizing: border-box;
}

.link {
  padding: 10px 0; 
  text-decoration: none;
  color: mediumslateblue;
  background-color: transparent;
}

.inputItem {
  height: 50px;
  outline: 1px solid #D0D0D0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gradeInput {
  width: 80%;
  height: 60%;
  border: none;
  border: 1px solid #D0D0D0;
  text-align: center;
}

.addButton {
  width: 68%;
  height: 60%;
  border: none;
  background-color: mediumslateblue;
  color: white;
}

.calculateButton {
  margin: 10px;
  background-color: mediumslateblue;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  flex: 1; 
  display: flex; 
  justify-content: center;
  align-items: center;
  text-align: center;
}

.studentTypeButtons {
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  margin-bottom: 20px;
  margin-top: 20px;
}


.studentTypeButtons button {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
  background-color: mediumslateblue;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.1em;
}










  
  